import React, { useState, useEffect } from "react";
import Container from "react-bootstrap/Container";
import { CarouselTermisur } from "./Carousel/CarouselTermisur";
import { Clientes } from "./Clientes/Clientes";
import { DividerTriangle } from "./Dividers/DividerTriangle";
import { Footer } from "./Footer/Footer";
import { NavbarTop } from "./NavbarTop";
import { RRSS } from "./RRSS/RRSS";
// eslint-disable-next-line
import { Proyectos } from "./Proyectos/Proyectos";
import { Servicios } from "./Servicios/Servicios";
import { ModalContacto } from "./ModalContacto/ModalContacto";

export const Main = () => {
  const [showButton, setShowButton] = useState(true);

  const [showModal, setShowModal] = useState(false);
  const handleCloseModal = () => setShowModal(false);
  const handleShowModal = () => {
    console.log("show modal");
    setShowModal(true);
    console.log(showModal);
  };

  const handleScroll = () => {
    const scrollTop = window.pageYOffset || document.documentElement.scrollTop;
    setShowButton(scrollTop > 300); // Muestra el botn cuando el scroll est ms abajo de 300 pxeles
  };

  const scrollToTop = () => {
    window.scrollTo({
      top: 0,
      behavior: "smooth",
    });
  };

  const handleClickScroll = (elementSelected) => {
    const element = document.getElementById(elementSelected);
    if (element) {
      let yOffset = -60; // Valor predeterminado para PC
      if (window.innerWidth <= 767) {
        // Dispositivos mviles con ancho de pantalla igual o menor a 767px
        yOffset = -300; // Ajusta este valor segn sea necesario para dispositivos mviles
      }
      const y =
        element.getBoundingClientRect().top + window.pageYOffset + yOffset;
      window.scrollTo({ top: y, behavior: "smooth" });
    }
  };

  useEffect(() => {
    window.addEventListener("scroll", handleScroll);
    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, []);

  return (
    <Container fluid id="inicio">
      <RRSS />
      <NavbarTop
        handleClickScroll={handleClickScroll}
        handleShowModal={handleShowModal}
      />
      <CarouselTermisur />
      <div id="seccion-servicios">
        <Servicios />
      </div>
      <div id="proyectos">
        <Proyectos />
      </div>
      <div id="seccion-clientes" style={{ marginBottom: "2rem" }}>
        <Clientes />
      </div>
      <DividerTriangle />

      <div id="seccion-nosotros">{/**<Nosotros /> */}</div>
      <Footer id="seccion-contacto"></Footer>
      {showButton && (
        <button className="scroll-top-button" onClick={scrollToTop}>
          <i className="fas fa-arrow-up"></i>
        </button>
      )}
      {showModal && (
        <ModalContacto
          showModal={showModal}
          handleCloseModal={handleCloseModal}
        />
      )}
    </Container>
  );
};
